/*--------------------------------------- General ---------------------------------------*/
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-align: center;
  list-style: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
a {
  text-decoration: none;
}

:root {
  --font-color-primary: #252745;
  --font-color-secondary: #60627a;
  --white-color: #f5f5f5;
  --red-color: #f04e44;
  --gray-color: #a8a9b7;
  --extralight-font: 100;
  --light-font: 200;
  --regular-font: 300;
  --medium-font: 400;
  --semibold-font: 500;
  --bold-font: 600;
  --extrabold-font: 700;
}

h1 {
  font-weight: 800;
  font-size: 4rem;
  padding-bottom: 2%;
}

/********************************** NAVIGATION BAR *************************************/
nav img {
  width: 50px;
  height: 50px;
}
@media screen and (max-width: 870px) {
  nav {
    display: none !important;
  }
}
#home-container .logo {
  width: 100px;
  height: 100px;
}
#home-container img {
  width: 100vw;
  height: 100vh;
}
#home-container h1 {
  border-bottom: 1px grey solid;
  max-width: 11ch;
  text-align: center;
  display: inline-block;
}
#home-container p {
  /* width: 70%; */
  display: inline;
}
#welcome-container {
  clip-path: polygon(0 0%, 100% 0, 100% 90%, 0% 100%);
  padding: 2% 0 5% !important;
  /* background-color: hsl(0, 1%, 90%); */
}
#what-we-do-container {
  /* clip-path: polygon(0 0%, 100% 0%, 100% 90%, 0% 100%); */
  /* background-color: hsl(0, 0%, 15%); */
}

.border-bottom {
  border-bottom: 1px grey solid;
  /* text-align: left; */
  width: 3ch;
}
.border-bottom-history {
  border-bottom: 1px grey solid;
  text-align: center !important;
  /* width: 3ch; */
}
#references-container {
  padding: 7% 0 5vw;
}
#references-container h1 {
  font-size: calc(2.5rem + 1vw); /*43px*/
}
.slider-bg {
  max-height: 140vh;
  padding-top: 7%;
}
.slider-bg .shadow {
  box-shadow: 0 10px 50px 0 rgba(97, 100, 148, 0.17);
}
.slider-bg h5 {
  padding: 0 17%;
}
.primary-button {
  padding: 7px 10px;
  color: #999;
  border: 1px #999 solid;
  background-color: white;
  border-radius: 5px;
  font-size: 1rem;
  transition: 0.15s ease-in-out;
}
.primary-button:hover {
  color: white;
  border: 1px white solid;
  background-color: #999;
  /* box-shadow: 0px 0px 5px 5px rgba(97, 100, 148, 0.3); */
}
h5 {
  color: rgb(60, 60, 60) !important;
}

#services-container {
  clip-path: polygon(0 5%, 100% 0, 100% 90%, 0% 100%);
  padding: 10vw 0 15vw !important;
  background-color: hsl(0, 1%, 90%);
}
#services-container h1 {
  padding-bottom: 2%;
}

#why-us-container {
  background-color: hsl(0, 0%, 100%);
}
#why-us-container img {
  border-radius: 5px;
}
#testimonials-container {
  clip-path: polygon(0 10%, 100% 0, 100% 100%, 0% 100%);
  padding: 28vw 0 15vw !important;
  background-image: url("../images/first-cont-white-overlay.jpg");
  /* width: 100%; */
  background-size: cover;
  width: 100vw;
  min-height: 125vh;
}
footer {
  background-color: hsl(0, 1%, 90%);
}
.padding-footer {
  padding: 1.5% 5%;
}
iframe {
  max-width: 90vw;
}
